// MQTT  
//  MQTT  

export const MQTT_CONFIG = {
  //   URL  ( )
  get broker() {
    // HTTPS  WSS (WebSocket Secure) 
    if (window.location.protocol === "https:") {
      // ngrok HTTPS 
      if (window.location.hostname.includes("ngrok")) {
        console.log(" ngrok HTTPS   - WSS  ");
        // ngrok TCP  WSS 
        // : wss://0.tcp.ngrok.io:19641
        const ngrokHost =
          localStorage.getItem("ngrok_mqtt_host") || "localhost";
        const ngrokPort = localStorage.getItem("ngrok_mqtt_port") || "9001";
        return `wss://${ngrokHost}:${ngrokPort}`;
      }

      //  HTTPS 
      console.log(" HTTPS   - WSS  ");
      return `wss://192.168.27.92:3000`;
    }

    // HTTP  WS (WebSocket) 
    console.log(" HTTP  () - WS  ");
    return `ws://192.168.27.92:3000`;
  },

  //   
  topic: "sandbox/digitaltwin",

  //  
  options: {
    clientId: `webxr-client-${Math.random().toString(16).substr(2, 8)}`,
    username: "", // MQTT   ( )
    password: "", // MQTT   ( )
    clean: true,
    connectTimeout: 5000, //   5 ()
    reconnectPeriod: 500, //   0.5 ()
    keepalive: 60, // 60 keepalive  
    reschedulePings: true, // ping  
    queueQoSZero: false, // QoS 0   
    protocolVersion: 4, // MQTT v3.1.1

    //    
    rejectUnauthorized: false,
    will: undefined, // will  

    // WebSocket   
    incomingStore: null, //    (QoS 0)
    outgoingStore: null, //    

    //   
    maxPacketSize: 65535, //   

    //    ( )
    batchSize: 1, //   1  
  },
};
