async function checkWebXRSupport() {
  if (!navigator.xr) {
    console.log("WebXR  -  3D  ");
    return false;
  }

  try {
    const vrSupported = await navigator.xr.isSessionSupported("immersive-vr");
    const arSupported = await navigator.xr.isSessionSupported("immersive-ar");

    if (vrSupported) {
      console.log("WebXR VR  ");
      xrMode = "vr";
      return true;
    } else if (arSupported) {
      console.log("WebXR AR  ");
      xrMode = "ar";
      return true;
    } else {
      console.log(
        "WebXR   VR/AR   -  3D  "
      );
      return false;
    }
  } catch (error) {
    console.log("WebXR    :", error);
    console.log(" 3D  ");
    return false;
  }
}

